.vertical-timeline {
  width: 95%;
  max-width: 1170px;
  margin: 2em auto;
  margin-top: 4em;
  position: relative;
  padding: 2em 0;
}
.vertical-timeline::after {
  /* clearfix */
  content: '';
  display: table;
  clear: both;
}

.vertical-timeline::before {
  /* this is the vertical line */
  content: '';
  position: absolute;
  top: 0;
  left: 18px;
  height: 100%;
  width: 4px ;
  background-image: linear-gradient(to top, transparent 50%, #fff 50%),
  linear-gradient(to top, #000033, #000033);
  background-size: 3px 20px, 100% 3px;
  border: none;

}

@media only screen and (min-width: 1170px) {
  .vertical-timeline.vertical-timeline--two-columns {
    margin-top: 3em;
    margin-bottom: 3em;
    width: 90%;
  }
  .vertical-timeline.vertical-timeline--two-columns:before {
    left: 50%;
    margin-left: -2px;
  }
}
